body {
    font-family: 'Arial', sans-serif; /* Change the font family */
    background-color: #f0f4f8; /* Light background for contrast */
    margin: 0;
    padding: 0;
  }
  
  .stylish-purchase-plan-page-container {
    max-width: 1200px; /* Limit max width of container */
    margin: 0 auto; /* Center the container */
    padding: 40px 20px; /* Padding for the container */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    background-color: #ffffff; /* White background for contrast */
    height: 100vh;
  }
  
  .stylish-purchase-plan-page-header {
    text-align: center;
    margin-bottom: 30px;
    font-size: 26px; /* Larger header font size */
    color: #333; /* Dark color for the header */
    font-weight: bold; /* Bold font weight */
  }
  
  .stylish-purchase-plan-page-plans {
    display: flex; /* Use flexbox to layout cards */
    flex-wrap: wrap; /* Allow cards to wrap */
    justify-content: space-around; /* Space cards evenly */
  }
  
  .stylish-purchase-plan-page-card {
    background: linear-gradient(145deg, #e6e9f0, #ffffff); /* Gradient background */
    border-radius: 15px; /* Rounded corners */
    padding: 20px; /* Internal padding */
    margin: 15px; /* External margin */
    width: calc(30% - 30px); /* Default width for larger screens */
    text-align: center; /* Center text */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Card shadow */
    transition: transform 0.3s, box-shadow 0.3s; /* Smooth transition */
  }
  
  .stylish-purchase-plan-page-card:hover {
    transform: translateY(-5px); /* Lift effect on hover */
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3); /* Darker shadow on hover */
  }
  
  .stylish-purchase-plan-page-card-header {
    font-size: 22px; /* Font size for headers */
    margin-bottom: 15px;
    color: #007bff; /* Header color */
    font-weight: bold; /* Bold header */
  }
  
  .stylish-purchase-plan-page-card-price {
    font-size: 20px; /* Font size for prices */
    color: #333; /* Price color */
    margin-bottom: 20px;
    font-weight: bold; /* Bold price */
  }
  
  
  .stylish-purchase-plan-page-card-button:hover {
    background-color: #0056b3; /* Darker color on hover */
    transform: scale(1.05); /* Slightly enlarge on hover */
  }
  .stylish-purchase-plan-page-card-button {
    padding: 6px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
}
  /* Responsive Styles */
  @media (max-width: 768px) {
    .stylish-purchase-plan-page-card {
      width: calc(45% - 30px); /* Full width for mobile */
    }
  }
  
  @media (max-width: 480px) {
    .stylish-purchase-plan-page-card {
      width: 100%; /* Full width for mobile */
    }
  
    .stylish-purchase-plan-page-header {
      font-size: 22px; /* Smaller header font size */
    }
  }
  